.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1em;
}

body {
    background-color: black;
    color: white;
}

.dragdrop {
    background-color: rgb(211, 60, 34);
    height: auto;
    text-align: center;
    width: 40%;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: bolder;
    border-radius: 20px;
    cursor: pointer;
}

.container-2 {
    margin-top: 100px;
    width: 100%;
    display: flex;
    gap: 30px;
    height: 100vh;
}

img {
    display: block;
    height: 100%;
    width: 100%;
    max-height: 250px;
    max-width: 250px;
    border-radius: 10px;
}

.left {
    display: flex;
    width: 22%;
    background-color: #1e1e1e;
    font-size: 18px;
    padding: 1em;
}

.left span{
    background-color: red;
    border-radius: 10px;
    cursor: pointer;
    transition: .2s ease;
}

.left span:hover{
    color: #1e1e1e;
    background-color: #e1e1e1;
}

.form span{
    margin-left: 10px;
    padding: 0.5em;
}

.form-container{
    display: inherit;
    flex-direction: column;
    display: none;
}

.form-container.active{
    display: flex;
}

.right {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    width: 100%;
}

.right-content {
    display: flex;
    flex-direction: column;
    width: 78%;
}

.prueba {
    background-color: #1d1d1f;
    justify-content: center;
    width: 100%;
    cursor: pointer;
    border: 3px solid #1d1d1f;
}

.prueba.active{
    animation: move 0.5s infinite;
}

@keyframes move{
    0%{
        transform: scale(1);
    }
    100%{
        transform: scale(1.09);
    }
}

.button{
    margin-top: 30px;
    padding: 0.5em;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
}

h2{
    margin: 0;
}

.card-stake {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.card-stake span {
    cursor: pointer;
    padding: 0.6em 2em;
    color: #fff;
    border-radius: 20px;
    transition: .2s ease;
}

.je {
    padding: 1em 1em;
    border-radius: 20px;
}

.upload-btn {
    padding: 0.5em 1em;
    background-color: #1e1e1e;
    border-radius: 10px;
    transition: .3s ease;
}

.upload-btn:hover {
    background-color: #e1e1e1;
    color: #1e1e1e;
}

.delete {
    background-color: red;
    font-weight: bold;
    text-transform: uppercase;
}

.delete:hover {
    background-color: #e1e1e1;
    color: black;
}

.pages {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    width: 100%;
    margin-bottom: 20px;
}

.page {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    gap: 15px;
}

.page span{
  color: white;
  cursor: pointer;
  font-size: 20px;
  transition: color .2 ease;
}

.page span:hover{
  color: rgb(5, 154, 253);
}

.indexpage.active{
  color: rgb(5, 154, 253);
}

.indexpage.change{
  color: white;
}